<template>
  <div :class="{ myheader: true, hideheader: headerHidden }">
    <router-link to="/my-personal-website-dist/" class="breadcrumb"
      >首頁</router-link
    >
    <svg
      v-if="$route.meta.breadcrumb"
      xmlns="http://www.w3.org/2000/svg"
      width="16"
      height="16"
      fill="currentColor"
      class="bi bi-arrow-right-short"
      viewBox="0 0 16 16"
      style="margin-bottom: 3px; margin-left: 3px"
    >
      <path
        fill-rule="evenodd"
        d="M4 8a.5.5 0 0 1 .5-.5h5.793L8.146 5.354a.5.5 0 1 1 .708-.708l3 3a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708-.708L10.293 8.5H4.5A.5.5 0 0 1 4 8z"
      />
    </svg>
    <span style="margin-left: 4px">{{ $route.meta.breadcrumb }}</span>
  </div>
</template>

<script>
export default {
  name: "MyHeader",
  props: { breadCrumb: String },
  data() {
    return {
      headerHidden: false,
      prevScrollY: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      // 當前的scroll值
      const currentScrollY = window.scrollY;

      if (currentScrollY > this.prevScrollY && !this.headerHidden) {
        this.headerHidden = true;
      } else if (currentScrollY < this.prevScrollY && this.headerHidden) {
        this.headerHidden = false;
      }

      this.prevScrollY = currentScrollY;
    },
    beforeUnmount() {
      window.removeEventListener("scroll", this.handleScroll);
    },
  },
};
</script>

<style scoped>
.myheader {
  background-color: rgb(221, 189, 101);
  margin-left: 350px;
  height: 3rem;
  position: fixed;
  width: 100%;
  top: 0;
  transition: 0.5s;
}
.hideheader {
  top: -3rem;
  transition: 0.5s;
}
.breadcrumb {
  display: inline;
  margin-left: 10rem;
  line-height: 3rem;
  font-family: "Noto Sans TC", sans-serif;
  font-size: 1.1rem;
  color: rgb(255, 255, 255);
}
.breadcrumb:hover {
  color: rgb(20, 124, 216);
  border-bottom: 2px solid rgb(20, 124, 216);
}
</style>
